:root body #wrapper {
margin: 0;
background-color: black;
outline: none;
border: none;
width: 100vw;
height: 100vh;
}
:root body #wrapper #image {
width: 100vw;
height: 100vh;
background-image: url("./Components/IMG_0320.jpg");
background-size: cover;
transform: translateX(var(--x)) translateY(var(--y)) scale(var(--scale));
transition: ease-out 0.7s;
}

#text-on-img {
    position: absolute;
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 90px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
    background-color: rgba(0, 0, 0, 0.9);
    z-index:20;
    color:black;
    border-radius: 5px;
}

#text-on-img p {
    font-family: 'Montserrat', sans-serif;
}
#text-on-img2 {
    font-size: 25px;
    float:right;
    font-weight: 600;
    color:white;
}

#text-on-img3 {
    color: rgb(255, 202, 252);
    text-shadow: 15px 15px 30px rgb(212, 0, 255);
    text-shadow: 5px 5px 30px  #985edb;
    margin-bottom: -25px;
}


@media only screen and (max-width: 900px) {
    #text-on-img{
        width: 80%;
    }
}

@media only screen and (max-width: 600px) {
    #text-on-img{
        width: 80%;
        font-size:50px;
    }
}