.Footer-cont {
    display:flex;
    justify-content: space-around;
    margin: 0 auto;
    width: 78%;
    margin-top: -80px;
    margin-bottom: 50px;
}

.Footer-cont1, .Footer-cont2 {
    color:white;
    font-size: 23px;
    padding: 20px;
    text-align: center;
}

.Footer-cont1 {
    border-right: 1px solid white;
}

.Footer-cont1 a {
    text-decoration: none;
    color: white;
}

.Footer-cont span:hover, .Footer-cont a:hover {
    font-size: 24px;
    color: rgb(163, 163, 163);
}


@media only screen and (max-width: 767px) {
    .Footer-cont1 {
        border:none;
    }
}