*{
    margin: 0;
    padding: 0;
    border: 0;
    font-weight: 100;
    font-family: 'Montserrat', sans-serif;
}
.parallax{
    background: url('./Resources/IMG_0192.jpg') repeat fixed 100%;
    background-size: cover;
    color: aliceblue;
}
.parallax-1{
    background: url('./Resources/IMG_0231.JPG') repeat fixed 100%;
    background-size: cover;
    color: aliceblue;
}
.parallax-2{
    background: url('./Resources/IMG_0291.JPG') repeat fixed 100%;
    background-size: cover;
    color: aliceblue;
}
.parallax-3{
    background: url('./Resources/IMG_0365.jpg') repeat fixed 100%;
    background-size: cover;
    color: aliceblue;
}
.parallax-4{
    background: url('./Resources/IMG_0319.jpg') repeat fixed 100%;
    background-size: cover;
    color: aliceblue;
}
.parallax-inner{
    padding-top: 20%;
    padding-bottom: 20%;
}

.parallax-info-1 {
    background-color: rgba(241, 187, 255, 0.8);
    width: 60%;
    margin: 0 auto;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 9px 8px #000000; 
    box-shadow: 0px 0px 9px 8px #000000;
}

.parallax-info-1 h1 {
    text-align: center;
    color: black;
}
.parallax-info-1 p {
    color: black;
    padding: 10px;
    line-height: 15px;
    margin-bottom: -5px;
    font-weight: 500;
    font-size: 18px;
}

.parallax-info-1 a {
    color: black;
    font-size: 19px;
}

.parallax-info-1 a:hover {
    color: black;
    font-size: 20px;
    transition: .7s;
}