.about-img {
    height: 400px;
    border-radius: 20px;
    float:left;
    opacity: 0.9;
}

.img-div {
    position: absolute;
    top: 20%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    margin-left: 10px;
}

.about-txt {
    color: white;
    align-self: center;
    margin: 40px;
    font-size: 20px;
}
.about-txt p {
    font-weight: 700;
}

@media only screen and (max-width: 950px) {
    .img-div{
        position:static;
        width: 100%;
    }
    .about-txt {
        margin:5px;
    }
}

.About-git {
    padding-left: 5px;
    color: white;
    font-weight: 700;
}

.About-git:hover {
    color: gray;
}

.About-home {
    color: rgb(143, 143, 255);
    font-size: 30px;
    margin-left: 50px;
}

.About-home:hover {
    color: rgb(83, 83, 177);
}

.about2-cont {
    color: white;
    margin-top: 100px;
    padding: 10px;
}

.about2-cont p{
    padding-left: 20px;
}

.about2-cont h2{
    color:brown;
}