body {
    background-color: black;
}

.Header-container {
    padding: 10px;
    color:white;
    margin-top: 100px;
    margin-bottom: 100px;
    font-size: 20px;
}
.Header-container1 {
    border-right: 1px solid gray;
    line-height: 22px;
}

.Header-container1 p{
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    padding-left: 15px;
}

.Header-h1 {
    font-family: 'Montserrat', sans-serif;
}

.Header-txt-change-1, .Header-txt-change-2, .Header-txt-change-3 {
    font-size: 90px;
    line-height: 55px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    
}
.Header-txt-change-1 {
    margin-left: 75px;
    font-size: 70px;
}

.Header-txt-change-3 {
    margin-left: 125px;
    font-size: 70px;
}

#stack {
    color: rgb(255, 154, 255);
}

.Header-link {
    font-size:24px;
    color: rgb(143, 143, 255);
}

.Header-link:hover {
    color: rgb(100, 100, 175);
    font-size:25px;
    transition: .5s;
}


@media only screen and (max-width: 991px) {
    .Header-container1 {
        border:none;
    }
}